import React from 'react'
import
{
    Input,
    Tabs,
    Descriptions, Icon,
} from 'antd'

export function ExportInfo(props)
{
    return (
        <Tabs
            type="card"
            style={props.tabsStyle}>
            <Tabs.TabPane
                key="info"
                tab="Thng tin">
                <Input
                    suffix={<Icon type="search" />}
                    placeholder='Tm nh cung cp'
                />
                <Descriptions
                    bordered
                    column={1}
                    layout="horizontal"
                    title="Thng tin nh CC">
                    <Descriptions.Item label="H v tn">
                        Nguyn Vn A
                    </Descriptions.Item>
                    <Descriptions.Item label="ST">
                        0987654321
                    </Descriptions.Item>
                    <Descriptions.Item label="a ch">
                        xxxxxxxxxxxxxxxx
                    </Descriptions.Item>
                </Descriptions>
            </Tabs.TabPane>
            <Tabs.TabPane
                key="note"
                tab="Ghi ch">
                <Input.TextArea
                    rows={15}
                    placeholder="Nhp ghi ch"
                />
            </Tabs.TabPane>
        </Tabs>
    )
}