import React from 'react'

import
{
    Table
} from 'antd'

export function TransactionHistory(props)
{
    const columns = [
        {
            title: "M ho n",
            dataIndex: "id",
        },
        {
            title: "Thi gian",
            dataIndex: "date",
        },
        {
            title: "Tng tin",
            dataIndex: "total"
        }
    ]

    return (
        <Table
            columns={columns}
            style={{ marginTop: 20 }}
            dataSource={props.dataSource}
            title={() => "Lch s giao dch"}
        />
    )
}