import React from 'react'
import
{
    Descriptions,
    DatePicker,
} from 'antd'

export function ExportDeliveryInfo(props)
{
    return (
        <>
            <Descriptions
                bordered
                column={1}
                layout="horizontal"
                title="Giao nhn">
                <Descriptions.Item label="Ngy xut kho">
                    <DatePicker format="DD-MM-YYYY" placeholder='Chn ngy xut kho' />
                </Descriptions.Item>
                <Descriptions.Item label="a ch xut">
                    xxxxxxxxxxxxxxxx
                </Descriptions.Item>
            </Descriptions>
        </>
    )
}