import React, {useState, useEffect} from "react";
import {
    AutoComplete,
    Button,
    Icon,
    Modal,
    Row,
    Table,
    Tooltip,
    Checkbox,
    Form,
    Input,
    InputNumber,
    DatePicker,
    Select, message
} from "antd";
import {useDispatch, useSelector} from "react-redux";
import {
    addEmployee, addEmployeeFailed, addEmployeeSuccess,
    chooseEmployee,
    getEmployee,
    getEmployeeFailed,
    getEmployeeSuccess,
    toggleAddModal,
} from "../../store/employee";

const AddEmployeeForm = Form.create()((props)=>{
    const {getFieldDecorator} = props.form;

    const dispatch = useDispatch();
    const [state, setState] = useState({confirmDirty: false});
    const validatePassword = (rule, value, callback) => {
        const {form} = props;
        if (value && value.length < 8){
            callback('Mt khu phi cha t nht 8 k t.');
        }else {
            callback();
        }

        if (value && state.confirmDirty){
            form.validateFields(['xacnhanmatkhau'], {force: true});
        }
    };

    const addModal = useSelector(state => state.employee.addModal);

    const confirmPassword = (rule, value, callback) =>{
        const {form} = props;

        if (value && value !== form.getFieldValue('matkhau')){
            callback('Mt khu khng trng khp!');
        }else {
            callback();
        }
    };

    const dateOfBirth = (rule, value, callback) =>{
        if (2019 - value.year() < 18)
            callback('Nhn vin phi ln hn 18 tui');
        else
            callback()
    };

    const handleConfirmBlur = e => {
        const {value} = e.target;
        setState({confirmDirty: state.confirmDirty || !!value});
    };

    const submitHandler = event =>{
        event.preventDefault();
        props.form.validateFields((err, values) =>
        {
            if (!err)
            {
                const info = {
                    ...values,
                    ngaysinh: `${values.ngaysinh.date()}/${values.ngaysinh.month() + 1}/${values.ngaysinh.year()}`,
                    anhdaidien: null,
                };

                dispatch(addEmployee(
                    null,
                    info,
                    (newEmployee) => {
                        dispatch(addEmployeeSuccess(newEmployee));
                        dispatch(toggleAddModal());
                        message.success('Thm nhn vin thnh cng.');
                    },
                    (err) => {
                        dispatch(addEmployeeFailed(err));
                    }
                ))
            }
        });
    };

    const formatter = (value) => `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ',');
    const parser = (value) => value.replace(/VN\s?|(,*)/g, '');

    return(
        <Form onSubmit={submitHandler} layout="vertical" labelAlign={"left"}>
            <Form.Item label="Tn ng nhp">
                {getFieldDecorator('tendangnhap', {
                    rules: [{ required: true, message: 'Vui lng in tn ng nhp!' }],
                })(<Input />)}
            </Form.Item>
            <Form.Item label="Mt khu" hasFeedback={true}>
                {getFieldDecorator('matkhau', {
                    rules: [
                        {
                            required: true,
                            message: 'Vui lng in mt khu!'
                        },
                        {
                            validator: validatePassword
                        }],
                })(<Input.Password />)}
            </Form.Item>
            <Form.Item label="Xc nhn mt khu" hasFeedback={true}>
                {getFieldDecorator('xacnhanmatkhau', {
                    rules: [
                        {
                            required: true,
                            message: 'Vui lng in li mt khu.'
                        },
                        {
                            validator: confirmPassword
                        },
                    ],
                })(<Input.Password onBlur={handleConfirmBlur} />)}
            </Form.Item>
            <Form.Item label={"H v tn"}>
                {getFieldDecorator('hoten', {
                    rules: [{ required: true, message: 'Vui lng in h tn.' }],
                })(<Input/>)}
            </Form.Item>
            <Form.Item label={"CMND"}>
                {getFieldDecorator('cmnd', {
                    rules: [{
                        required: true,
                        message: 'Vui lng in mt khu.'
                    },
                        {
                            pattern: '\\d',
                            message: 'S CMND khng hp l.'
                        }
                    ],
                })(<Input/>)}
            </Form.Item>
            <Form.Item>
                <Form.Item label={"Ngy sinh"}
                           style={{display: 'inline-block', width: 'calc(50% - 12px)'}}
                           hasFeedback={true}>
                    {getFieldDecorator('ngaysinh', {
                        rules: [{
                            required: true
                        }, {
                           validator:  dateOfBirth
                        }],
                    })(<DatePicker format='DD-MM-YYYY'/>)}
                </Form.Item>
                <Form.Item label={"Gii tnh"} style={{display: 'inline-block', width: 'calc(50% - 12px)'}}>
                    {getFieldDecorator('gioitinh', {
                        rules: [{
                            required: true
                        }],
                    })(<Select>
                        <Select.Option value={'NAM'}>NAM</Select.Option>
                        <Select.Option value={'NU'}>N</Select.Option>
                        <Select.Option value={'KHONGRO'}>KHNG R</Select.Option>
                    </Select>)}
                </Form.Item>
            </Form.Item>
            <Form.Item>
                <Form.Item label={"ST"} hasFeedback={true}
                           style={{
                               display: 'inline-block',
                               width: 'calc(50% - 12px)',
                               paddingRight: '40px'
                           }}>
                    {getFieldDecorator('sdt', {
                        rules: [{
                            required: true,
                            message: 'Vui lng in s in thoi.'
                        },
                            {
                                pattern: '^(0|\\+84)(\\s|\\.)?((3[2-9])|(5[689])|(7[06-9])|(8[1-689])|(9[0-46-9]))(\\d)(\\s|\\.)?(\\d{3})(\\s|\\.)?(\\d{3})$',
                                message: 'S in thoi khng hp l.'
                            }
                        ],
                    })(<Input/>)}
                </Form.Item>
                <Form.Item label={"Chc v"} style={{display: 'inline-block', width: 'calc(50% - 12px)'}}>
                    {getFieldDecorator('chucvu', {
                        rules: [{
                            required: true
                        }],
                    })(<Select>
                        <Select.Option value={1}>NHN VIN BN HNG</Select.Option>
                        <Select.Option value={2}>NHN VIN QUN L NHN S</Select.Option>
                        <Select.Option value={3}>NHN VIN QUN L KHO</Select.Option>
                        <Select.Option value={4}>GIM C</Select.Option>
                    </Select>)}
                </Form.Item>
            </Form.Item>
            <Form.Item>
                <Form.Item label={"a ch"}
                           style={{
                               display: 'inline-block',
                               width: 'calc(50% - 12px)',
                               paddingRight: '40px'
                           }}>
                    {getFieldDecorator('diachi', {
                        rules: [{
                            required: true,
                            message: 'Vui lng in mt khu.'
                        }],
                    })(<Input/>)}
                </Form.Item>
                <Form.Item label={"Lng c bn"}
                           style={{
                               display: 'inline-block',
                               width: 'calc(50% - 12px)'
                           }}>
                    {getFieldDecorator('luongcoban', {
                        rules: [{
                            required: true,
                            message: 'Vui lng in mt khu.'
                        }],
                    })(<InputNumber
                        style={{
                            width: '100%'
                        }}
                        formatter={value => formatter(value)}
                        parser={parser}
                        min={0}
                        step={100000}
                    />)}
                </Form.Item>
            </Form.Item>
            <Form.Item>
                <Button
                    size='large'
                    shape='round'
                    htmlType="submit"
                    loading={addModal.loading}
                    style={{
                        backgroundColor: '#42376A',
                        color: 'white',
                        width: '100%',
                        marginTop: 20
                    }}
                >
                    ng k
                </Button>
            </Form.Item>
            <Form.Item
                validateStatus= {addModal.err ? 'error' : null}
                help={addModal.err ? addModal.err.message : ''} />
        </Form>
    )
});

export function EmployeeTable(props) {

    const columns = [
        {
          dataIndex: 'chosen',
          fixed: 'left',
          render: (value)=>{
              return (<Checkbox checked={value}/>)
          }
        },
        {
          title: "M nhn vin",
          dataIndex: "manv"
        },
        {
            title: "Tn nhn vin",
            dataIndex: "tennv",
        },
        {
            title: "Chc v",
            dataIndex: "chucvu",
            filters: [
                {
                    text: 'Nhn vin bn hng',
                    value: 1
                },
                {
                    text: 'Nhn vin qun l nhn s',
                    value: 2
                },
                {
                    text: 'Nhn vin qun l kho',
                    value: 3
                },
                {
                    text: 'Gim c',
                    value: 4
                }
            ],
            filterMultiple: true,
            onFilter: (value, record) => record.chucvu === value,
            render: (value) =>{
                let result = '';
                if (value === 1)
                    result = 'Nhn vin bn hng';
                else if(value === 2)
                    result = 'Nhn vin qun l nhn s';
                else if (value === 3)
                    result = 'Nhn vin qun l kho';
                else if (value === 4)
                    result = 'Gim c';
                return result;
            }
        },
    ];

    const dispatch = useDispatch();
    const [state, setState] = useState({
        searchDataSource: null,
        tableDataSource: null
    });

    const employees = useSelector(state => state.employee.employees.map((data,key) =>{
        return{
            manv: `NV00${key}`,
            tennv: data.taikhoan.hoten,
            chucvu: data.chucvu,
            key: data.key,
            chosen: data.chosen
        }
    }));

    const addModal = useSelector(state => state.employee.addModal);

    useEffect(() =>{
        dispatch(getEmployee(
            null,
            (data) => dispatch(getEmployeeSuccess(data)),
            (err) => dispatch(getEmployeeFailed(err))
        ))
    },[dispatch]);

    const searchHandler = (value) =>{
        if (value || value !== ''){
            const pattern = new RegExp(value);
            const list = employees
                .filter(data => data.manv.match(pattern) || data.tennv.match(pattern))
                .map(data => {
                    if (data.manv.match(pattern)) {
                        return data.manv;
                    } else {
                        return data.tennv;
                    }
                });

            setState({
                ...state,
                searchDataSource: list
            });
        }else
            setState({
                ...state,
                tableDataSource: null
            });
    };

    const selectHandler = (value) =>{
        const pattern = new RegExp(value);
        const list = employees
            .filter(data => data.manv.match(pattern) || data.tennv.match(pattern));
        setState({
            ...state,
            tableDataSource: list
        });
    };

    const rowClickHandler = (record, rowIndex)=>{
        dispatch(chooseEmployee(record.key));
        if (state.tableDataSource){
            setState({
                ...state,
                tableDataSource: state.tableDataSource.map((value, key) =>{
                    if (key === rowIndex)
                        return{
                            ...value,
                            chosen: !value.chosen
                        };
                    else
                        return {
                            ...value,
                            chosen: false
                        }
                })
            })
        }
    };

    return (
        <>
            <AutoComplete
                allowClear={true}
                suffix={<Icon type="search" />}
                placeholder='Nhp tn hoc m nhn vin'
                style={{width: '100%'}}
                dataSource={state.searchDataSource ? state.searchDataSource.toArray() : []}
                onSearch={searchHandler}
                onSelect={selectHandler}
            />
            <Row
                type="flex"
                justify="start"
                align="middle"
                style={{marginTop: 20}}
            >
                <Tooltip
                    placement="top"
                    title="Thm nhn vin"
                >
                    <Button
                        type="primary"
                        icon="plus"
                        style={{ marginRight: 10 }}
                        onClick={() => dispatch(toggleAddModal())}
                    />
                </Tooltip>
                {
                    // single product modal
                    <Modal
                        visible={addModal.visible || addModal.loading}
                        onOk={() => dispatch(toggleAddModal())}
                        onCancel={()=> dispatch(toggleAddModal())}
                        centered
                        closable
                        title="Thm nhn vin"
                        footer={null}
                    >
                        <AddEmployeeForm/>
                    </Modal>
                }
            </Row>
            <Table
                columns={columns}

                onRow={(record, rowIndex)=>({
                    onClick: event => {
                        event.preventDefault();
                        rowClickHandler(record, rowIndex)
                    }
                })}

                style={{ marginTop: 20 }}
                dataSource={state.tableDataSource ? state.tableDataSource.toArray() : employees.toArray()}
            />
        </>
    )
}