import React from 'react'
import
{
    Table,
} from 'antd'


export function BillDetails(props)
{
    const columns = [
        {
            title: "Tn sn phm",
            dataIndex: "name",
        },
        {
            title: "S lng",
            dataIndex: "amount",
        },
        {
            title: 'Thnh tin',
            dataIndex: 'price',
        }
    ];

    return (
        <Table
            columns={columns}
            style={{ marginTop: 5 }}
            dataSource={props.dataSource}
            title={() => `Chi tit ho n ${props.billID}`}
        />
    )
}