import React from 'react'
import { Tabs } from 'antd'

import { Tab } from '../tab'

import report from '../../images/report.png'
import employee from '../../images/user.png'
import bill from '../../images/bill.png'
import client from '../../images/client.png'

import './DirectorPageContent.css'

const { TabPane } = Tabs;

export function DirectorPageContent(props) {
    return (
        <Tabs>
            <TabPane tab={
                <Tab src={report} text="BO CO"/>
            } key="bill">
                {/*<Bill />*/}
            </TabPane>

            <TabPane tab={
                <Tab src={employee} text="QUN L NHN VIN" />
            } key="service">
                {/*<Service />*/}
            </TabPane>

            <TabPane tab={
                <Tab src={bill} text="QUN L PHIU" />
            } key="user">
                {/*<Client />*/}
            </TabPane>

            <TabPane tab={
                <Tab src={client} text="QUN L KHCH HNG" />
            } key="chart">
                {/*<Bill />*/}
            </TabPane>
        </Tabs>
    )
}