import React from 'react'
import
{
    Tabs,
    Descriptions,
} from 'antd'

export function PaymentInfo(props)
{
    return (
        <Tabs
            type="card"
            style={props.tabsStyle}>
            <Tabs.TabPane
                key="payment"
                tab="Thanh ton">
                <Descriptions
                    bordered
                    column={1}
                    layout="horizontal">
                    <Descriptions.Item label="VAT">
                        %
                    </Descriptions.Item>
                    <Descriptions.Item label="Chit khu">
                        %
                    </Descriptions.Item>
                    <Descriptions.Item label="Tng tin">
                        VN
                    </Descriptions.Item>
                    <Descriptions.Item label="Tin thi">
                        VN
                    </Descriptions.Item>
                    <Descriptions.Item label="Khch a">
                        VN
                    </Descriptions.Item>
                </Descriptions>
            </Tabs.TabPane>
            <Tabs.TabPane
                key="inDebt"
                tab="N">
                <Descriptions
                    bordered
                    column={1}
                    layout="horizontal">
                    <Descriptions.Item label="VAT">
                        %
                    </Descriptions.Item>
                    <Descriptions.Item label="Chit khu">
                        %
                    </Descriptions.Item>
                    <Descriptions.Item label="Tng tin">
                        VN
                    </Descriptions.Item>
                </Descriptions>
            </Tabs.TabPane>
        </Tabs>
    )
}