import React from 'react'
import
{
    Input,
    Tabs,
    Descriptions,
    Button
} from 'antd'

export function PaymentInfo(props)
{
    return (
        <Tabs
            type="card"
            style={props.tabsStyle}>
            <Tabs.TabPane
                key="payment"
                tab="Thanh ton">
                <Descriptions
                    bordered
                    column={1}
                    layout="horizontal">
                    <Descriptions.Item label="VAT">
                        %
                                    </Descriptions.Item>
                    <Descriptions.Item label="Chit khu">
                        %
                                    </Descriptions.Item>
                    <Descriptions.Item label="Tng tin">
                        VN
                                    </Descriptions.Item>
                    <Descriptions.Item label="Tin thi">
                        VN
                                    </Descriptions.Item>
                </Descriptions>
                <Input
                    style=
                    {{
                        marginTop: 10,
                    }}
                    suffix="VN"
                    placeholder="Khch a"
                />
                <Button
                    style={{
                        backgroundColor: "#42376A",
                        marginTop: 20,
                        width: "100%"
                    }}
                    type="primary">Thanh ton
                                </Button>
            </Tabs.TabPane>
            <Tabs.TabPane
                key="inDebt"
                tab="N">
                <Descriptions
                    bordered
                    column={1}
                    layout="horizontal">
                    <Descriptions.Item label="VAT">
                        %
                                    </Descriptions.Item>
                    <Descriptions.Item label="Chit khu">
                        %
                                    </Descriptions.Item>
                    <Descriptions.Item label="Tng tin">
                        VN
                                    </Descriptions.Item>
                </Descriptions>
                <Button
                    style={{
                        backgroundColor: "#42376A",
                        marginTop: 20,
                        width: "100%"
                    }}
                    type="primary">
                    Ghi n
                                </Button>
            </Tabs.TabPane>
        </Tabs>
    )
}