import React from "react";
import {useSelector} from "react-redux";

import {
    Card,
    Badge,
    Descriptions, Result, Row
} from 'antd'

import goldProduct from '../../images/goldProduct.jpg'

export function ProductInfo(props){
    const productInfo = useSelector(state => state.product.productInfo);
    const {Item} = Descriptions;

    console.log(productInfo);
    return(
        productInfo.data ? (
            <Card
                hoverable={true}
                cover={<img alt={'goldProduct'} src={goldProduct}/>}
                loading={productInfo.loading}
            >
                {
                    productInfo.data &&
                    <Descriptions
                        // style={{maxHeight: '100xp'}}
                        column={1}
                        layout={'horizontal'}
                        bordered={true}>
                        <Item style={{fontWeight: 'bold'}} label={'ID SN PHM'}>{productInfo.data.idsp}</Item>
                        <Item label={'TN SN PHM'}>{productInfo.data.tensp}</Item>
                        <Item label={'LOI SN PHM'}>{productInfo.data.loaisp}</Item>
                        <Item label={'S LNG'}>{productInfo.data.soluong}</Item>
                        <Item label={'GI NHP'}>{productInfo.data.gianhap}</Item>
                        <Item label={'GI BN'}>{productInfo.data.giaban}</Item>
                        <Item label={'TNH TRNG'}>{
                            productInfo.data.tinhtrang > 0 ?
                                <Badge status="processing" text="Cn hng" /> :
                                <Badge status="error" text="Ht hng" />
                        }</Item>
                        <Item label={'TIU CHUN'}>{productInfo.data.tieuchuan}</Item>
                        <Item label={'KHI LNG'}>{productInfo.data.khoiluong}</Item>
                        <Item label={'GHI CH'}>{productInfo.data.ghichu}</Item>
                    </Descriptions>
                }
            </Card>
        ) : (
            <Row type={"flex"} justify={'center'} align={'middle'}>
                <Result
                    status="404"
                    title="404"
                    subTitle="ID sn phm khng tn ti."
                />
            </Row>
        )
    )
}