import React from 'react'
import
{
    Descriptions,
    DatePicker,
    Button
} from 'antd'

export function DeliveryInfo(props)
{
    const buttonStyle = {
        backgroundColor: "#42376A",
        marginTop: 10,
        float: "right"
    }
    return (
        <>
            <Descriptions
                bordered
                column={1}
                layout="horizontal"
                title="Giao nhn">
                <Descriptions.Item label="Ngy nhn">
                    <DatePicker format="DD-MM-YYYY" placeholder='Chn ngy nhn' />
                </Descriptions.Item>
                <Descriptions.Item label="Ngy giao">
                    <DatePicker format="DD-MM-YYYY" placeholder='Chn ngy giao' />
                </Descriptions.Item>
                <Descriptions.Item label="a ch nhn">
                    xxxxxxxxxxxxxxxx
                </Descriptions.Item>
                <Descriptions.Item label="a ch giao">
                    xxxxxxxxxxxxxxxx
                </Descriptions.Item>
            </Descriptions>
            <Button type="primary" style={buttonStyle}>Khch ly lin</Button>
        </>
    )
}