import React from 'react'
import
{
    Input,
    Tabs,
    Descriptions, Icon,
} from 'antd'

export function DeliveryInfo(props)
{
    return (
        <Tabs
            type="card"
            style={props.tabsStyle}>
            <Tabs.TabPane
                key="deliver"
                tab="Ngi giao">
                <Input
                    suffix={<Icon type="search" />}
                    placeholder='Nhp m hoc tn nhn vin'
                />
                <Descriptions
                    bordered
                    column={1}
                    layout="horizontal"
                    title="Thng tin nhn vin">
                    <Descriptions.Item label="H v tn">
                        Nguyn Vn A
                    </Descriptions.Item>
                    <Descriptions.Item label="ST">
                        0987654321
                    </Descriptions.Item>
                    <Descriptions.Item label="a ch">
                        xxxxxxxxxxxxxxxx
                    </Descriptions.Item>
                </Descriptions>
            </Tabs.TabPane>
            <Tabs.TabPane
                key="receiver"
                tab="Ngi nhn">
                <Input
                    suffix={<Icon type="search" />}
                    placeholder='Nhp m hoc tn nhn vin'
                />
                <Descriptions
                    bordered
                    column={1}
                    layout="horizontal"
                    title="Thng tin nhn vin">
                    <Descriptions.Item label="H v tn">
                        Nguyn Vn A
                    </Descriptions.Item>
                    <Descriptions.Item label="ST">
                        0987654321
                    </Descriptions.Item>
                    <Descriptions.Item label="a ch">
                        xxxxxxxxxxxxxxxx
                    </Descriptions.Item>
                </Descriptions>
            </Tabs.TabPane>
        </Tabs>
    )
}