import React from 'react'
import
{
    Descriptions,
    DatePicker,
} from 'antd'

export function ImportDeliveryInfo(props)
{
    return (
        <>
            <Descriptions
                bordered
                column={1}
                layout="horizontal"
                title="Giao nhn">
                <Descriptions.Item label="Ngy yu cu">
                    <DatePicker format="DD-MM-YYYY" placeholder='Chn ngy yu cu' />
                </Descriptions.Item>
                <Descriptions.Item label="Ngy nhn">
                    <DatePicker format="DD-MM-YYYY" placeholder='Chn ngy nhn' />
                </Descriptions.Item>
                <Descriptions.Item label="a ch nhp">
                    xxxxxxxxxxxxxxxx
                </Descriptions.Item>
            </Descriptions>
        </>
    )
}