import React from 'react'
import
{
    Table,
} from 'antd'

export function InventoryTable(props)
{
    const columns = [
        {
            title: "M sn phm",
            dataIndex: "id",
        },
        {
            title: "Tn sn phm",
            dataIndex: "name",
        },
        {
            title: "S lng",
            dataIndex: "amount"
        }
        ,
        {
            title: "Ngy tn",
            dataIndex: "days"
        },
        {
            title: "Loi sn phm",
            dataIndex: "category",
            filters: [
                {
                    text: 'Loi A',
                    value: 'Loi A'
                },
                {
                    text: 'Loi B',
                    value: 'Loi B'
                },
                {
                    text: 'Loi C',
                    value: 'Loi C'
                },
            ],
            filterMultiple: true,
            onFilter: (value, record) => record.category === value
        },
    ];

    return (
        <Table
            columns={columns}
            style={{ marginTop: 20 }}
            dataSource={props.dataSource}
            title={() => "Danh sch hng tn kho"}
        />
    )
}