import React from 'react'
import
{
    Input,
    Tabs,
    Descriptions,
} from 'antd'

import { SearchBar } from './SearchBar'

export function ClientInfo(props)
{
    return (
        <Tabs
            type="card"
            style={props.tabsStyle}>
            <Tabs.TabPane
                key="client"
                tab="Khch hng">
                <SearchBar placeHolder="Tm khch hng" />
                <Descriptions
                    bordered
                    column={1}
                    layout="horizontal"
                    title="Thng tin khch hng">
                    <Descriptions.Item label="H v tn">
                        Nguyn Vn A
                                    </Descriptions.Item>
                    <Descriptions.Item label="ST">
                        0987654321
                                    </Descriptions.Item>
                    <Descriptions.Item label="a ch">
                        xxxxxxxxxxxxxxxx
                                    </Descriptions.Item>
                </Descriptions>
            </Tabs.TabPane>
            <Tabs.TabPane
                key="note"
                tab="Ghi ch">
                <Input.TextArea
                    rows={15}
                    placeholder="Nhp ghi ch"
                />
            </Tabs.TabPane>
        </Tabs>
    )
}