import React from 'react'
import
{
    Table,
    InputNumber,
} from 'antd'

export function ProductTable(props)
{
    const columns = [
        {
            title: "Tn sn phm",
            dataIndex: "name",
        },
        {
            title: "S lng",
            dataIndex: "amount",
            render: (text, record, index) =>
            {
                return (
                    <InputNumber
                        min={1}
                        defaultValue={text}
                        // onChange={(value) =>
                        // {
                        //     onChange(value, text, record, index)
                        // }}
                        // formatter={(value) =>
                        // {
                        //     return parseInt(value, 10)
                        // }}
                        // parser={(value) =>
                        // {
                        //     return parseInt(value, 10)
                        // }}
                    />
                )
            }
        },
        {
            title: "n gi",
            dataIndex: "price"
        },
        {
            title: 'Loi sn phm',
            dataIndex: 'category',
            filters: [
                {
                    text: 'Loi A',
                    value: 'Loi A'
                },
                {
                    text: 'Loi B',
                    value: 'Loi B'
                },
                {
                    text: 'Loi C',
                    value: 'Loi C'
                },
            ],
            filterMultiple: true,
            onFilter: (value, record) => record.category === value
        }
    ]
    return (
        <Table
            columns={columns}
            style={{ marginTop: 20 }}
            dataSource={props.dataSource}
        />
    )
}